import React, { useState } from "react";
import { Link } from "react-router-dom";
import SidebarTab from "./SidebarTab";
import { allTabs } from "./SidebarTabs";

function Sidebar({ permissions, hidden, setHidden, user }) {
	const [showdrop, setShowDrop] = useState([]);

	const toggleDrop = (id) => {
		let exist = showdrop.find((x) => x === id);
		if (exist) {
			setShowDrop(showdrop.filter((x) => x !== id));
		} else {
			setShowDrop([...showdrop, id]);
		}
	};
	return (
		<aside class={`sidebar ${hidden ? "hidden" : ""}`}>
			<div class="sidebar-start">
				<div class="sidebar-head">
					<a href="/" class="logo-wrapper" title="Home">
						<span class="sr-only">Home</span>
						<span class="icon logo" aria-hidden="true"></span>
						<div class="logo-text">
							<span class="logo-title">Elegant</span>
							<span class="logo-subtitle">Dashboard</span>
						</div>
					</a>
					<button
						class={`sidebar-toggle transparent-btn ${hidden ? "rotated" : ""}`}
						title="Menu"
						type="button"
						onClick={() => setHidden(!hidden)}
					>
						<span class="sr-only">Toggle menu</span>
						<span class="icon menu-toggle" aria-hidden="true"></span>
					</button>
				</div>
				<div class="sidebar-body">
					<ul class="sidebar-body-menu">
						<li>
							<Link class="active" to="/">
								<span class="icon home" aria-hidden="true"></span>Dashboard
							</Link>
						</li>
						{permissions &&
							allTabs.map((tab) => {
								let found = permissions.find(
									(perm) => perm === tab?.permission
								);
								if (found) {
									return (
										<SidebarTab
											permissions={permissions}
											toggleDrop={toggleDrop}
											showdrop={showdrop}
											hidden={hidden}
											tab={tab}
										/>
									);
								}
								return;
							})}
					</ul>
				</div>
			</div>
			<div class="sidebar-footer mt-3">
				<a href="##" class="sidebar-user">
					<span class="sidebar-user-img">
						<img
							src={user?.profile_social}
							style={{ borderRadius: "15px" }}
							alt="User name"
						/>
					</span>
					<div class="sidebar-user-info">
						<span class="sidebar-user__title">{user?.username}</span>
					</div>
				</a>
			</div>
		</aside>
	);
}

export default Sidebar;
