import React from "react";
import { Link } from "react-router-dom";

function SidebarTab({ tab, toggleDrop, hidden, showdrop, permissions }) {
	return (
		<li>
			<a
				class="show-cat-btn text-center"
				href="#"
				onClick={() => toggleDrop(tab.toggle)}
			>
				<span
					style={{ fontSize: "24px" }}
					class={tab.icon}
					aria-hidden="true"
				></span>
				{tab.name}
				<span
					class={`category__btn transparent-btn ${
						showdrop.includes(tab.toggle) ? "rotated" : ""
					}`}
					title="Open list"
				>
					<span class="sr-only">Open list</span>
					<span class="icon arrow-down" aria-hidden="true"></span>
				</span>
			</a>
			<ul
				class={`cat-sub-menu ${
					!hidden && showdrop.includes(tab.toggle) ? "visible" : ""
				}`}
			>
				{tab.drops.map((drop) => {
					let found = permissions.find((perm) => perm === drop?.permission);
					if (found) {
						return (
							<li>
								<Link to={drop.link}>{drop.name}</Link>
							</li>
						);
					}
					return;
				})}
			</ul>
		</li>
	);
}

export default SidebarTab;
